body {
  margin: 0;
  padding: 0;
  font-family: "century gothic", sans-serif;
  background-color: yellow;
}

h1 {
  text-align: center;
  font-size: 50px;
  color: blue;
  text-shadow: 3px 3px 2px lime;
}

/*Styles the buttons*/
.Buttons{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  width: 500px;
  margin: 0 auto;
  margin-top: 25px;
}

button {
  border-radius: 5px;
  background-color: white;
  border: 3px solid red;
  outline: none;
  color: red;
  padding: 20px;
  font-size: 25px;
  font-weight: bold;
  -webkit-box-shadow: 7px 7px 3px pink;
          box-shadow: 7px 7px 3px pink;
}

button:hover{
  border-radius: 5px;
  background-color: red;
  border: 3px solid white;
  color: white;
  -webkit-box-shadow: 7px 7px 3px white;
          box-shadow: 7px 7px 3px white;
  outline: none;
}

.PreviousButton:disabled, .NextButton:disabled {
  display: none;
}

/*Styles the card and background*/
.Card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  width: 650px;
  height: 350px;
  background-color: white;
  border: 5px dashed red;
  border-radius: 5px;
  margin: 0 auto;
  -webkit-box-shadow: 7px 7px 3px pink;
          box-shadow: 7px 7px 3px pink;
  padding: 5px;
}

/*Syles the question text*/
.CardText {
  text-align: center;
  font-size: 60px;
  -ms-flex-item-align: center;
      align-self: center;
  margin-top: 45px;
  width: 600px;
  height: 325px;
  padding-top: 20px;
}

/*Styles the card number and the side of the card.*/
.CardNumber {
  padding-top: 10px;
  -ms-flex-item-align: end;
      align-self: flex-end;
}

p {
  font-size: 18px;
  color: blue;
  padding: 0;
  margin: 0 auto;
}



